import React, {Component} from "react"
import Section from "../components/Section";
import Publication from "../components/Publication"

const publications = [
    {
        name: "Joint analysis of multiple high-dimensional data types using sparse matrix approximations of rank-1 with applications to ovarian and liver cancer",
        authors: "Okimoto, G & Zeinalzadeh, A & Wenska, T & Loomis, M & Nation, J & Fabre, T & Tiirikainen, M & Hernandez, B & Chan, O & Wong, L & Kwee, S. (2016)",
        link: "https://biodatamining.biomedcentral.com/articles/10.1186/s13040-016-0103-7"
    },
    {
        name: "A Neural Network Model to Classify Liver Cancer Patients Using Data Expansion and Compression",
        authors: "Zeinalzadeh, A & Wenska, T & Okimoto, G. (2016)",
        link: "https://www.researchgate.net/publication/310769643_A_Neural_Network_Model_to_Classify_Liver_Cancer_Patients_Using_Data_Expansion_and_Compression"
    }
];

class Publications extends Component {
    render() {
        return (
            <Section {... this.props}>
                {publications.map(
                    publication => (
                        <Publication  {...publication}/>
                    ))}
            </Section>
        )
    }
}

export default Publications