import React, {Component} from "react";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import logo from "../static/logo.png";

class Navigation extends Component {
    state = {
        height: document.documentElement.clientHeight
    }

    handleScroll = () => {
        const Logo = document.getElementById("Logo");
        if ((document.body.scrollTop / this.state.height) >= .95 || (document.documentElement.scrollTop / this.state.height) >= .95) {
            Logo.style.height = '30px';
        } else {

            Logo.style.height = (document.documentElement.scrollTop / this.state.height) * 30 + 'px';
        }
    }

    componentDidMount = () => {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount = () => {
        window.removeEventListener('scroll', this.handleScroll);
    }

    render() {
        return (
            <Navbar collapseOnSelect expand="sm" bg="secondary" variant="dark" className="sticky-top">
                <Navbar.Brand href="#home">
                    <img
                        id="Logo"
                        alt="Logo"
                        src={logo}
                        height="0"
                        className="d-inline-block align-top px-3"
                    />
                </Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav"/>
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="ml-auto">
                        <Nav.Link href="#home">Home</Nav.Link>
                        <Nav.Link href="#about">About</Nav.Link>
                        <Nav.Link href="#team">Team</Nav.Link>
                        <Nav.Link href="#publications">Publications</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        )
    }
}

export default Navigation