import React, {Component} from "react"
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import Section from "../components/Section";
import Col from "react-bootstrap/Col";


class About extends Component {
    render() {
        return (
            <Section {...this.props}>
                <Col className="text-justify" sm={12}>
                    Immune checkpoint blockade (ICB) is a form of immunotherapy that has resulted in extraordinary gains
                    in clinical response and overall survival in a growing number of cancers. Unfortunately, only a
                    minority of patients respond favorably to these costly and potentially toxic treatments. Molecular
                    biomarkers of different types that predict beforehand who will  derive a durable clinical benefit
                    from these revolutionary treatments are urgently needed.

                </Col>
                <Col className="my-4 text-justify" sm={12}>
                    <b>SNR Analytics Inc.</b> (SNRAI) develops sparse signal processing (SSP) techniques that are designed to
                    identify small subsets of molecular variables in big genomic datasets (i.e., sparse signatures)
                    that are predictive of immunotherapeutic response. We have found that sparse signatures discovered
                    by our SSP algorithms are highly enriched for biology associated with the immune status of the
                    tumor microenvironment (TME) and response to immunotherapy.
                </Col>
                <Col className="my-4" sm={12} md={4} data-aos="flip-down">
                    <FontAwesomeIcon
                        className="text-primary"
                        icon="wave-square"
                        size="8x"
                    />
                    <p className="text-justify">
                        SNRAI data scientists and software engineers develop data-driven models that leverage the
                        surprising connection between signal sparsity and the immune status of the tumor
                        microenvironment (TME) to better predict how a cancer patient will respond to ICB and other
                        immunotherapies.
                    </p>
                </Col>
                <Col className="my-4" sm={12} md={4} data-aos="flip-down">
                    <FontAwesomeIcon
                        className="text-primary"
                        icon="dna"
                        size="8x"
                    />
                    <p className="text-justify">
                        SNRAI is developing an internal database of immunological signatures that are likely predictors
                        of ICB response and/or overall survival in multiple cancers based on the analysis of all cancers
                        in The Cancer Genome Atlas using an analysis pipeline based on SSP techniques.
                    </p>
                </Col>
                <Col className="my-4" sm={12} md={4} data-aos="flip-down">
                    <FontAwesomeIcon
                        className="text-primary"
                        icon="cloud"
                        size="8x"
                    />
                    <p className="text-justify">
                        SNRAI is developing a cloud-based version of a SSP pipeline that runs in Jupyter notebooks to
                        facilitate the wider use of SSP techniques in biomarker discovery and predictive modeling of
                        cancer and other complex diseases.
                    </p>
                </Col>
            </Section>
        )
    }
}

export default About