import React, {Component} from "react"
import Section from "../components/Section";
import TeamMember from "../components/TeamMember.js"

import logo from "../static/headshot/user.png"
import okimoto from "../static/headshot/okimoto.jpg"
import wenska from "../static/headshot/wenska.jpg"
import emory from "../static/headshot/emory.jpg"
import michael from "../static/headshot/michael.jpg"
import spencer from "../static/headshot/spencer.jpg"

const teamMembers = [
    {
        profile: okimoto,
        name: 'Gordon Okimoto, PhD',
        title: 'President / Chief Scientific Officer',
        link_type: 'researchgate',
        link: 'https://www.researchgate.net/profile/Gordon_Okimoto'
    },
    {
        profile: wenska,
        name: 'Thomas Wenska, PhD',
        title: 'Data Scientist',
        link_type: 'researchgate',
        link: 'https://www.researchgate.net/profile/Tom_Wenska2'
    },
    {
        profile: logo,
        name: 'Ashkan Zeinalzadeh, PhD',
        title: 'Data Scientist',
        link_type: 'researchgate',
        link: 'https://www.researchgate.net/profile/Ashkan_Zeinalzadeh'
    },
    {
        profile: emory,
        name: 'Emory Zitello',
        title: 'Biologist',
        link_type: 'researchgate',
        link: 'https://www.researchgate.net/profile/Emory_Zitello'
    },
    {
        profile: michael,
        name: 'Michael Vo',
        title: 'Lead Software Engineer',
        link_type: 'github',
        link: 'https://github.com/mmvo91'
    },
    {
        profile: spencer,
        name: 'Spencer Long',
        title: 'Software Engineer',
        link_type: 'github',
        link: 'https://github.com/spencer-long'
    }
];

class Team extends Component {
    render() {
        return (
            <Section {...this.props}>
                {
                    teamMembers.map(
                        teamMember => (
                            <TeamMember {...teamMember}/>
                        )
                    )
                }
            </Section>
        )
    }
}

export default Team