import React, {Component} from "react"
import logo from '../static/logo.png';
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Button from "react-bootstrap/Button";
import Image from "react-bootstrap/Image";

class Hero extends Component {
    state = {
        height: document.documentElement.clientHeight
    }

    handleScroll = () => {
        const Logo = document.getElementById("hero-logo");
        if ((document.body.scrollTop / this.state.height) <= .95 || (document.documentElement.scrollTop / this.state.height) <= .95) {
            Logo.style.width = (40 - ((document.documentElement.scrollTop / this.state.height) * 40)) + '%';
        } else {
            Logo.style.width = '0%';
        }
    }

    componentDidMount = () => {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount = () => {
        window.removeEventListener('scroll', this.handleScroll);
    }

    render() {
        return (
            <Container fluid className="hero bg-secondary py-4">
                <Row className="py-3 h-100">
                    <Col className="my-auto">
                        <Image id="hero-logo" src={logo} width="40%" fluid/>
                        <div className="h3 py-5">
                            Sparse signal processing for precision medicine
                        </div>
                        <Button href="mailto:gokimoto@snrai.com">Contact Us</Button>
                    </Col>
                </Row>
            </Container>
        )
    }
}

export default Hero