import React, {Component} from 'react';
import './custom.scss';
import './App.css';
import Container from "react-bootstrap/Container";
import {library} from '@fortawesome/fontawesome-svg-core'
import {fab} from '@fortawesome/free-brands-svg-icons'
import {faWaveSquare, faDna, faCloud} from '@fortawesome/free-solid-svg-icons'
import Navigation from "./sections/Navigation";
import Hero from "./sections/Hero";
import About from "./sections/About";
import Team from "./sections/Team";
import Publications from "./sections/Publications"
import Footer from "./sections/Footer";

import AOS from 'aos';
import 'aos/dist/aos.css';

library.add(fab, faWaveSquare, faDna, faCloud);

class App extends Component {
    componentDidMount() {
        AOS.init({
            duration: 1200,
            mirror: true,
        });
    }

    render() {
        return (
            <Container fluid id="home" className="App p-0">
                <Navigation/>
                <Hero/>
                <About className="bg-white" id="about" name="About"/>
                <Team className="bg-primary" id="team" name="Team"/>
                <Publications className="bg-white" id="publications" name="Publications"/>
                <Footer/>
            </Container>
        );
    }
}

export default App;
